/** MUSE Player i18n framework */
export const i18nTexts = {
  default: {
    backward: '',
    currentLyricOffset: '',
    disabled: '',
    devMode: '',
    devModeAlert: '\n\n' +
          '',
    enabled: '',
    exit: '',
    forward: '',
    fullscreenMode: '',
    looping: '',
    modulation: '',
    playRate: '',
    setLyricOffset: '',
    stop: '',
    updateAvailable: ''
  },
  chineseTraditional: {
    backward: '',
    currentLyricOffset: '',
    disabled: '',
    devMode: '',
    devModeAlert: '\n\n' +
          '',
    enabled: '',
    exit: '',
    forward: '',
    fullscreenMode: '',
    looping: '',
    modulation: '',
    playRate: '',
    setLyricOffset: '',
    stop: '',
    updateAvailable: ''
  },
  english: {
    backward: '',
    currentLyricOffset: 'Current',
    disabled: 'Disabled',
    devMode: 'Developer mode',
    devModeAlert: 'This operation will remove right-click listener so that you can inspect the player. You can refresh the page and activate the right-click listener again.\n\n' +
          'Enable developer mode?',
    enabled: 'Enable',
    exit: 'Exit',
    forward: '',
    fullscreenMode: 'Fullscreen mode',
    looping: 'Loop',
    modulation: 'Modulation',
    playRate: 'Playing rate',
    setLyricOffset: 'Lyric offset',
    stop: 'Stop',
    updateAvailable: 'Update available'
  },
  japanese: {
    backward: '',
    currentLyricOffset: '',
    disabled: '',
    devMode: '',
    devModeAlert: '\n\n' +
          '',
    enabled: '',
    exit: '',
    forward: '',
    fullscreenMode: '',
    looping: '',
    modulation: '',
    playRate: '',
    setLyricOffset: '',
    stop: '',
    updateAvailable: ''
  }
};

export const i18n = (key, lang = undefined) => {
  lang = lang ? lang : window.navigator.languages[0] || window.navigator.language || 'zh-CN';
  switch (lang)
  {
    case 'zh-CN':
    case 'zh':
    default:
      return i18nTexts.default[key];

    case 'zh-HK':
    case 'zh-TW':
      return i18nTexts.chineseTraditional[key];

    case 'ja':
    case 'ja-JP':
      return i18nTexts.japanese[key];

    case 'en-US':
    case 'en-GB':
    case 'en-CA':
    case 'en-AU':
    case 'en-SA':
    case 'en-NZ':
    case 'en':
      return i18nTexts.english[key];
  }
};

export default i18n;
